
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
 }

 .section-hero {
    display: flex;
    background-color: #fafafa;
    margin-top: 80px;
    margin-left: 32px;
    margin-right: 32px;
    max-width: 1200px;
    flex-shrink: 0;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: linear-gradient(180deg, rgba(17, 17, 17, 0.00) 0%, rgba(17, 17, 17, 0.40) 100%), url(/src/images/desk_bground.jpg);
    border-radius: 30px;
    
 }




 .hero_text {
    color: #FFF;
    font-family: Montserrat;
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: 34px; /* 121.429% */
    letter-spacing: -0.84px;
    width: 484px;
 }



 .buy-now {
    width: 144px;
    height: 45px;
    background-color: var(--orange);
    border:none;
    color: #FFF;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    border-radius: 100px; 
    display: flex;
    padding: 14px 40px;
    align-items: flex-start;
    gap: 14px;
    cursor: pointer;
 }

 .buy-now:hover,
 .buy-now:focus {
    background: #FCF1E0;
    color: var(--orange)
 }

 .how-its-made {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 183px;
    height: 45px;
   

    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    color: #FFFFFF;
    border-radius: 100px;
    border: 1px solid #FFFFFF;
    background-color: transparent;
    cursor: pointer;
    
 }

.how-its-made:hover, 
.how-its-made:focus {
    border: 1px solid #FD9222;
    color: var(--orange);
    background-color: none;

}


 .scroll-down {
    width: 38px;
    height: 38px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
}

.scroll-down-text {
    color: #FFF;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px; /* 128.571% */
    letter-spacing: -0.42px;
    gap: 14px;
 }
 
 .hero-scroll-icon {
    /*lalala*/
 }

 .container-hero-one {
    width: 1056px;
    height: 179px;
    margin-top: max(381px);
    margin-bottom: 0;
    gap: 32px;
 }

 .container-hero-two {
   display: flex;
   gap: 14px;
   margin-top: 32px;
   justify-content: space-between;
}

.container-hero-three {
   justify-content: center;
   align-items: center;
   height: 53px;
   width: 163px;
   display: flex;
   gap: 14px;
   border-radius: 10px;
   border: transparent;
   background: transparent;
}

.container-hero-three:hover,
.container-hero-three:focus {
   color: var(--orange);
   cursor: pointer;
   background-color: none;
}

.button-div-bye-now {
   display: flex;
   gap: 14px;
}