body {
  color: #111;
  background-color: #fafafa;
  font-family: Montserrat, sans-serif;
}

* {
  box-sizing: border-box;
  max-width: 100%;
  height: auto;
}

.main {
  justify-items: center;
  display: flex;
}

.container {
  width: 1136px;
  height: 600px;
  padding: 0 32px;
}

.section {
  max-width: 1200px;
  padding-bottom: 128px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

:root {
  --orange: #fd9222;
  --black: #111;
  --dark-purple: #1e1823;
  --white-gray: #fafafa;
  --white: #fff;
}



.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section-hero {
  max-width: 1200px;
  background-color: #fafafa;
  background-image: linear-gradient(#1110 0%, #1116 100%), url("desk_bground.7fb2fa1b.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 30px;
  flex-shrink: 0;
  margin-top: 80px;
  margin-left: 32px;
  margin-right: 32px;
  display: flex;
}

.hero_text {
  color: #fff;
  letter-spacing: -.84px;
  width: 484px;
  font-family: Montserrat;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 34px;
}

.buy-now {
  width: 144px;
  height: 45px;
  background-color: var(--orange);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 100px;
  align-items: flex-start;
  gap: 14px;
  padding: 14px 40px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  display: flex;
}

.buy-now:hover, .buy-now:focus {
  color: var(--orange);
  background: #fcf1e0;
}

.how-its-made {
  width: 183px;
  height: 45px;
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  display: flex;
}

.how-its-made:hover, .how-its-made:focus {
  color: var(--orange);
  background-color: none;
  border: 1px solid #fd9222;
}

.scroll-down {
  width: 38px;
  height: 38px;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.scroll-down-text {
  color: #fff;
  letter-spacing: -.42px;
  gap: 14px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.container-hero-one {
  width: 1056px;
  height: 179px;
  gap: 32px;
  margin-top: 381px;
  margin-bottom: 0;
}

.container-hero-two {
  justify-content: space-between;
  gap: 14px;
  margin-top: 32px;
  display: flex;
}

.container-hero-three {
  height: 53px;
  width: 163px;
  background: none;
  border: #0000;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.container-hero-three:hover, .container-hero-three:focus {
  color: var(--orange);
  cursor: pointer;
  background-color: none;
}

.button-div-bye-now {
  gap: 14px;
  display: flex;
}



